.user-card {
 width: 305px;
 box-shadow: 0 1px 4px 0 rgba(51, 51, 51, .15);
 border-radius: 10px;
 list-style: none;
 text-decoration: none;
}

.user-card__description {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 36px;
}

.user-card__image {
  width: 124px;
  height: 124px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
}

.user-card__title {
  font-size: 20px;
  line-height: 1.15;
  font-weight: 400;
  margin: 16px 0;
}

.user-card__link {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.user-card__like {
  width: 30px;
  height: 28px;
  padding: 0;
  background: #F8F8F8 url('../../images/like.svg') center no-repeat;
  border: 0;
  border-radius: 4px;
  cursor: pointer;
  margin: 0 auto 20px 84%;
}

.user-card__like_active {
  background: #F8F8F8 url('../../images/likeActive.svg') center no-repeat;
}